import { Base64Utils } from '../utils/base64-utils';
import { FgBaseAPI } from './base-api';

export class FgLoggerAPI extends FgBaseAPI {
  protected _afterLoad() {
    this.baseUrl += 'api/logger/';
  }

  createSession() {
    return this._post('session', {});
  }

  addRawEntry(logId, token, rawEntry) {
    const entry = Object.assign({}, rawEntry, { sessionId: logId });
    const headers = this._getSessionExtraHeaders(logId, token);
    return this._post('entry', entry, headers);
  }

  addEntry(logId, token, type, message, category, _date) {
    const headers = this._getSessionExtraHeaders(logId, token);
    return this._post('entry', {
      sessionId: logId,
      type,
      message,
      category,
      date: _date || new Date(),
    }, headers);
  }

  closeSession(id, token) {
    const headers = this._getSessionExtraHeaders(id, token);
    return this._delete(`session/${id}`, undefined, headers);
  }

  private _getSessionExtraHeaders(logId, token) {
    if (!token) {
      return null;
    }

    const finalToken = Base64Utils.stringToBase64(`${logId}:${token}`);
    const headers: Record<string, string> = {
      'x-logger-session': finalToken
    };

    return headers;
  }
}